.hero-banner {
  margin-bottom: 4rem;
}

.hero-banner-single_container {
  padding: 2rem 0;
  box-sizing: border-box;
}

.hero-banner-single-content {
  margin-bottom: 2rem;
}

.hero-banner-single-content .title-container {
  color: #FCFCFC;
  text-align: center;
}

.hero-banner-single-content .arrow-down-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 3.125rem;
}

.hero-banner-single-content .arrow-down-container .arrow-down {
  width: 56px;
  height: 56px;
  background-color: #fcfcfc;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  border-radius: 50%;
  cursor: pointer;
  transition: transform 0.5s ease;
}

.hero-banner-single-content .arrow-down-container .arrow-down:hover {
  transform: scale(1.1);
}

@media (min-width: 992px) {
  .hero-banner {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
  }
  .hero-banner-single_container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    gap: 4rem;
  }
  .hero-banner-single_container .hero-banner-single-content {
    margin-bottom: 0;
  }
  .hero-banner-single_container .hero-banner-single-content .title-container h1 {
    font-size: 64px;
    font-weight: 700;
    line-height: 88px;
    letter-spacing: 1px;
  }
  .hero-banner-single_container .hero-banner-single-content .title-container h2 {
    font-size: 20px;
    font-weight: 400;
    line-height: 32px;
    letter-spacing: 0.75px;
  }
}
@media (min-width: 1232px) {
  .hero-banner-single {
    height: 650px;
  }
  .hero-banner-single_container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
  }
  .hero-banner-single_container .hero-banner-single-content {
    max-width: 678px;
    margin: 0 auto;
  }
}